import React,{Component} from 'react';
import PostForm from './postForm'

class PostCreate extends Component{
	
	render(){
		return(
			<React.Fragment>
				<h1>Create New Post</h1>
				<PostForm />
			</React.Fragment>
			);
	}
}
export default PostCreate;


