import React,{Component} from 'react';
import moment from 'moment';
class PostForm extends Component{
	constructor(props){
		super(props);
		var {post}=this.props;
		if(post !== undefined){
			this.state={
				draft:post.draft,
				title:post.title,
				content:post.content,
				publish:post.publish,
				errors:null,
			}
		}else{
			this.state={
				draft:false,
				title:'',
				content:'',
				publish:null,
				errors:null,
			}
		}
		this.handleSubmit=this.handleSubmit.bind(this);
		this.handleInput=this.handleInput.bind(this);
		this.updatePost=this.updatePost.bind(this);
		this.createPost=this.createPost.bind(this);
		this.deletePost=this.deletePost.bind(this);
		this.getCookie=this.getCookie.bind(this);
		this.clearForm=this.clearForm.bind(this);
		this.postTitleRef=React.createRef();
		this.postContentRef=React.createRef();
		}
	
	getCookie(name){
		var cookieValue=null;
		if(document.cookie && document.cookie !==null){
			var cookies=document.cookie.split(';');
			for(var i=0; i<cookies.length; i++){
				var cookie=cookies[i].trim();
				if(cookie.substring(0, name.length + 1)===(name+'=')){
					cookieValue=decodeURIComponent(cookie.substring(name.length+1));
					break;
				}
			}
			return cookieValue;
		}
	}
	deletePost(data){
		var {post}=this.props;
		var thisComp=this;
		var url=`http://localhost:8000/api/${post.slug}/`;

		var answer=prompt(`please enter name as ${post.title} to delete ?`)
		var csrftoken=this.getCookie('csrftoken');
		if (csrftoken !== undefined & answer===post.title){
			var lookUpOptions={
			method:'DELETE',
			headers:{
				'Content-Type':'application/json',
				'X-CSRFToken':csrftoken,
			},
			body:JSON.stringify(data),
			// credentials: 'include'
			};
			fetch(url, lookUpOptions).then(function(response){
				if(response.status === 204){
					window.location.href="/"
				}else{
				return response.json();
			}
			})
			.then(responseData=>{
				if(responseData.detail){
					this.setState({errors:responseData.detail,})
				}
			})
			.catch(error=>{
				this.setState({errors:error,})
			});
		}else{
		this.setState({errors:'Put the title of the post in Confirm Box,Thanx.'})
	}
		
	}//deletePost ends
	updatePost(data){
		var {post}=this.props;
		var thisComp=this;
		var url=`http://localhost:8000/api/${post.slug}/`;
		var csrftoken=this.getCookie('csrftoken');
		if (csrftoken !== undefined){
			var lookUpOptions={
			method:'PUT',
			headers:{
				'Content-Type':'application/json',
				'X-CSRFToken':csrftoken,
			},
			body:JSON.stringify(data),
			// credentials: 'include'
			};
			fetch(url, lookUpOptions).then(response=>response.json())
			.then(responseData=>{
				if(responseData.detail){
					this.setState({errors:responseData.detail,})
				}
				if(thisComp.props.postItemUpdated){
					thisComp.props.postItemUpdated(responseData);
				}
			})
			.catch(error=>{
				this.setState({errors:error,})
			});
		}
		
	}//updatePost ends
	createPost(data){
		var thisComp=this;
		var url=`http://localhost:8000/api/`;
		var csrftoken=this.getCookie('csrftoken');
		if (csrftoken !== undefined){
			var lookUpOptions={
			method:'POST',
			headers:{
				'Content-Type':'application/json',
				'X-CSRFToken':csrftoken,
			},
			body:JSON.stringify(data),
			// credentials: 'include'
			};
			fetch(url, lookUpOptions).then(response=>response.json())
			.then(responseData=>{
				if(responseData.detail){
					this.setState({errors:responseData.detail,})
				}
				thisComp.defaultState();
				thisComp.clearForm();
			})
			.catch(error=>{
				this.setState({errors:error,})
			});
		}
		
	}//createPost ends
	handleSubmit(event){
		event.preventDefault();
		var data=this.state;
		if(this.props.post){
			this.updatePost(data);
		}else{
		this.createPost(data);
		}
		console.log(data);
	}
	handleInput(event){
		event.preventDefault();
		if(event.target.name ==='draft'){
			var draftValue=(event.target.value==='on'? true:false)
			this.setState({
				[event.target.name]:draftValue,
			});
		}
		else{
			if(this.state.draft===undefined){
			this.setState({
				draft:false
			});
			};
			this.setState({
			[event.target.name]:event.target.value,
		});
		}
		
	}
	clearForm(event){
		if(event){
			event.preventDefault();
		}
		console.log("clearing")
		this.createPostForm.reset();
		this.defaultState();
	}
	defaultState(){
		this.setState({
			draft:false,
			title:'',
			content:'',
			publish:moment(new Date()).format('YYYY-MM-DD'),
		});
	}
	componentDidMount(){
		var {post}=this.props;
		if(post !== undefined){
		this.setState({
			draft:post.draft,
			title:post.title,
			content:post.content,
			publish:moment(post.publish).format('YYYY-MM-DD'),});
			console.log('i dont know')
		}else{
		console.log("you know")
		this.defaultState();
	}
		// this.titleForm.current.value='khanji';
	}
	render(){
	const {publish}= this.state;
	var {title}=this.state;
	var {content}=this.state;
	var {errors}=this.state;
	var {post}=this.props;
	return(
		<React.Fragment>
			<p className={`alert alert-danger ${(errors !== null)?'':'d-none'}`}>{errors}</p>
			<form onSubmit={this.handleSubmit} ref={(el)=>{this.createPostForm=el;}} className="p-5 col-md-6">
				<div className="form-group">
					<label htmlFor="title">Post Title</label>
					<input type="text" ref={this.titleForm} required onChange={this.handleInput} value={title} name='title' className="form-control" placeholder="Post title" />
				</div>
				<div className="form-group">
					<label htmlFor="content">Post Content</label>
					<textarea type="text" ref={this.contentForm} value={content} required onChange={this.handleInput} name='content' className="form-control" placeholder="Post content"></textarea>
				</div>
				<div className="form-group">
					<input type="radio" checked={this.state.draft} onFocus={this.handleInput} name='draft' value="on"  />
					<label className="mr-3" htmlFor="draft">Draft</label>
					<input type="radio" checked={!this.state.draft} onFocus={this.handleInput} name='draft' value="off" />
					<label className="mr-3" htmlFor="draft">UnDraft</label>
				</div>
				<div className="form-group">
					<label htmlFor="publish">Published Date</label>
					<input type="date" value={publish} required onChange={this.handleInput} name='publish' className="form-control" />
				</div>
				<input type="submit" className="btn btn-primary" value={`${(post!==undefined)?"Update":"Create"}`} />
			</form>
			<button onClick={()=>{this.deletePost(post);}} className={`btn btn-danger ml-5 ${(post === undefined)?"d-none":""}`}>Delete</button>
			<button onClick={this.clearForm} className="ml-5 btn btn-warning">clear</button>
		</React.Fragment>
	);
	}
}
export default PostForm;


