import React,{Component} from 'react';
import {Link} from 'react-router-dom';

class PostInline extends Component{
	render(){
		var {post}=this.props;
		var {elClass}=this.props;
		var isCard = elClass==='card' ? true:false;
		console.log(post.slug)
		return(
			<React.Fragment>
				<div className={elClass}>
					<h3>
						<Link  
						to={{pathname:`/posts/${post.slug}`,state:{fromDashboard:false }
							}}>{post.title}</Link></h3>
					{isCard ? <p>{post.content}</p>:''}
				</div>
			</React.Fragment>
			
			)
	}

}

export default PostInline;


