import React,{Component} from 'react'
import PostInline from './components/postInline';
import PostForm from './components/postForm';
import {Link} from 'react-router-dom';
class FrontApp extends Component{
	
	constructor(props){
		super(props);
		this.state={
			next:null,
			previous:null,
			author:false,
			count:0,
			postList:[],
			myClass:'card',

		};
		this.fetchPosts=this.fetchPosts.bind(this);
		this.getCookie=this.getCookie.bind(this);
		this.toggleClass=this.toggleClass.bind(this);
		this.newPost=this.newPost.bind(this);
		this.loadMore=this.loadMore.bind(this);
	}
	loadMore(){
		var {next}=this.state;
		this.fetchPosts(next);
		console.log('as u wish->',this.state)
	}
	fetchPosts(nextEndPoint){
		var url='http://localhost:8000/api/';
		if(nextEndPoint !== undefined){
			url=nextEndPoint;
		}
		console.log(url);
		var myComp=this;
		var lookUpOptions={
			method:'GET',
			headers: {
				'Content-Type':'application/json',
			},
		}
		fetch(url,lookUpOptions).then(response=>response.json())
		.then(data=>{
				var currentPosts=myComp.state.postList;
				myComp.setState({
					next:data.next,
					previous:data.prev,
					author:data.author,
					count:data.count,
					postList:currentPosts.concat(data.results),
				})
			})
		.catch((error)=>console.log("error",error));
	}
	getCookie(name){
		var cookieValue=null;
		if(document.cookie && document.cookie !==null){
			var cookies=document.cookie.split(';');
			for(var i=0; i<cookies.length; i++){
				var cookie=cookies[i].trim();
				if(cookie.substring(0, name.length + 1)===(name+'=')){
					cookieValue=decodeURIComponent(cookie.substring(name.length+1));
					break;
				}
			}
			return cookieValue;
		}
	}
	getCookie2(){
		if(document.cookie && document.cookie !==null){
			var cookies=document.cookie.split(';');
			for(var i=0; i<cookies.length; i++){
				var cookie=cookies[i].trim();
				console.log(cookies[i])
			}
		}
	}
	toggleClass(event){
		event.preventDefault();
		var mythis=this;
		var old=this.state.myClass;
		if(old === ''){
			mythis.setState({
				myClass:'card',
			});
		}else{
			mythis.setState({
				myClass:'',
			})
		}
	}
	newPost(newData){
		console.log(newData);
		var prevPosts=this.state.postList;
		prevPosts.unshift(newData);
		this.setState({
			postList:prevPosts,
		});
	}
	componentDidMount(){
		this.setState({
			next:null,
			previous:null,
			author:false,
			count:0,
			postList:[],
			myClass:'card',
		})
		this.fetchPosts();
	}
	render(){
		var {postList}=this.state;
		var {myClass}=this.state;
		var csrftoken=this.getCookie('csrftoken');
		var {next}=this.state;
		var {author}=this.state;
		return(
		<React.Fragment>
			<div className="container-fluid">
				<h1>Hey Welcome to jsx</h1>
				{(author !== false)?<Link className='btn btn-link p-5' to={{pathname:'/create'}}>Make New Post</Link>:""}
				<button className='btn btn-link p-5' onClick={this.toggleClass}>Toggle</button>
				<ul>
					{postList.length > 0 ? '' :<div>
					<br/><br/>
					<p className='alert alert-danger'>No post's</p>
					</div>}
					{postList.map((post, index)=>
						<React.Fragment key={index}>
							<PostInline post={post} elClass={myClass}/>
						</React.Fragment>
					)}
					{(next !== null)?
						<button className="btn btn-success" onClick={this.loadMore}>Load More</button>:
						""}
				</ul>
			</div>
		</React.Fragment>
		)
	}
	
}

export default FrontApp;


