import React,{Component} from 'react';
import {Link} from 'react-router-dom';
import PostForm from './postForm';

class PostDetail extends Component{
	constructor(props){
		super(props);
		this.state={
			slug:null,
			post:[],
			doneLoading:false,
			error:null,
		}
		this.fetchPostDetail=this.fetchPostDetail.bind(this);
		this.getCookie=this.getCookie.bind(this);
		this.handlePostItemUpdated=this.handlePostItemUpdated.bind(this);
	}
	getCookie(name){
		var cookieValue=null;
		if(document.cookie && document.cookie !==null){
			var cookies=document.cookie.split(';');
			for(var i=0; i<cookies.length; i++){
				var cookie=cookies[i].trim();
				if(cookie.substring(0, name.length + 1)===(name+'=')){
					cookieValue=decodeURIComponent(cookie.substring(name.length+1));
					break;
				}
			}
			return cookieValue;
		}
	}
	fetchPostDetail(){
		var slug=this.props.match.params.slug;
		var url='http://localhost:8000/api/'+slug+'/';
		console.log(url)
		var csrftoken=this.getCookie('csrf-token');
		fetch(url,{
			method:'GET',
			headers:{
				'Content-Type':'application/json',
				'X-CSRFToken':csrftoken,
			},
		})
		.then((response)=>response.json())
		.then((respData)=>{console.log(respData);
		if(respData.detail){this.setState({post:null,error:respData.detail,doneLoading:true,})}
		else{this.setState({post:respData,doneLoading:true,})}
		})
		.catch((error)=>console.log("errorsss",error));
	}

	handlePostItemUpdated(data){
	this.setState({
		post:data,
	});
	}
	componentDidMount(){
		if(this.props.match){
			this.setState({
				slug:this.props.match.params.slug,
				post:null,
				error:null,
			});
		}else{
			this.setState({slug:null,post:null,doneLoading:false});
		}
		this.fetchPostDetail();
	}	
	render(){
		var {slug}=this.state;
		var {post}=this.state;
		var {doneLoading}=this.state;
		var {error}=this.state;
		console.log(post);
		return(
			<React.Fragment>
				{(doneLoading === true ?
					<div>
					{(error === null) ?
						<div>
							<h4>{(slug !== null)?<div><h1>{post.title}</h1><br/>{post.content}</div>: "Not Found"}
							{(post.owner === true ?
								<div>
									<Link className='btn btn-link p-5' to={{pathname:'/create'}}>Make New Post</Link>
									<PostForm postItemUpdated={this.handlePostItemUpdated} post={post}/>
								</div>:"")}
							</h4>
							<Link to={{pathname:'/'}} className="btn btn-primary">All Posts</Link></div>
							:<h2 className="alert alert-danger">
							{error}
							</h2>}
						</div>
					:<h5>Loading...</h5>)}
			</React.Fragment>
		);
	}	
}		
		
export default PostDetail;


